
body {
    background: #e0e0e0;
}
.muirtl-16lloyr-MuiBottomNavigation-root {
    border-radius: 20px;
    background: #e0e0e0;
    box-shadow:  20px 20px 60px #bebebe,
                 -20px -20px 60px #ffffff;
    width: 98%;
    position: fixed;
    bottom: 1%;
    left: 1%;
    right: auto;
    height: 80px !important;
    border-top: 3px solid #4b38c2;
    z-index: 10020;
}
.navb {
    a {
        margin-inline: 20px;
        color: #ccc !important;
    }
    a:nth-child(2) {
        button {
            height: 75px;
            border-radius: 50px;
            background: #654fef;
            box-shadow:  4px 4px 40px #5543cb6c,
                         -4px -4px 40px #745bff71;
            
            color: #fff;
            min-width: 0;
            border: 4px solid #0b005049;
            width: 75px;
        }
    }
    svg {
        width: 40px;
        height: 40px;
    }
}
.muirtl-oh8qid-MuiButtonBase-root-MuiButton-root {
    color: #5c5c5c !important;
}
.navb {
    a.active {
        .muirtl-oh8qid-MuiButtonBase-root-MuiButton-root {
            color: #654fef !important;
        }
    }
}
.muirtl-11xur9t-MuiPaper-root-MuiTableContainer-root {
    border-radius: 50px;
background: #e0e0e0;
box-shadow:  4px 4px 10px #bebebe,
             -4px -4px 10px #ffffff !important;
}
.center-items {
    display: flex;
    align-items: center;
    label {
        color: #323232;
        font-weight: 500;
        margin-left: 15px;
    }
}
.range-off {
    label {
        font-weight: bold;
        color: #323232;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-inline: 10px;
    width: 100%;
}
.top-salsd {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.login {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
tbody tr{
    transition: 0.1s ease;
    &:hover {
        background: #2980b9!important;
        color: #fff !important;
    }
}
td {
    font-size: 14px;
    text-align: center;
    cursor: pointer;
    border-left: 1px solid #dddddd !important;
}
.text-bold {
    font-weight: bold;
}
thead tr {
    background: #2980b9;
    color: #fff;
    font-size: 14px;
    &:hover {
        background: cornflowerblue !important;
        color: #fff !important;
    }
    th {
        padding: 10px;
        border-color: #fff;
        text-align: center;
    }
}

.muirtl-1ji7omv-MuiBackdrop-root {
    backdrop-filter: blur(5px);
    background-color: #ffffff41 !important;
}
.muirtl-1hc7nu0-MuiPaper-root-MuiAppBar-root {
    z-index: 100000 !important;
}
.muirtl-yiavyu-MuiBackdrop-root-MuiDialog-backdrop {
    background: #fff5 !important;
    backdrop-filter: blur(3px);
    z-index: 200000;
}
.modal-s {
    z-index: 100000 !important;
    .modal-header .btn-close {
        margin: 0 !important;
        padding-left: 20px;
    }
}
.printStyle {
    font-size: 18px;
}
.cfd {
    background: #2980b9 !important;
    color: #fff !important;
    &:hover {
        background-color: rgb(253, 27, 27) !important;
    }
}
.active____Table {
    background: cornflowerblue !important;
    color: #fff !important;
}